import { motion } from "framer-motion";
import Backdrop from "../Backdrop/index";

const dropIn = {
  hidden: {
    y: "100vh",
    opacity: 0,
  },
  visible: {
    y: "0",
    opacity: 1,
    transition: {
      duration: 0.1,
      type: "spring",
      damping: 25,
      stiffness: 500,
    },
  },
  exit: {
    y: "100vh",
    opacity: 0,
  },
};

const Modal = ({ handleClose, setShowConfetti }) => {

  function closeModal() {
    setShowConfetti(false);
    handleClose();
  }

  return (
    <Backdrop onClick={() => closeModal()}>
      <motion.div
        onClick={(e) => e.stopPropagation()}  // Prevent click from closing modal
        className="modal orange-gradient"
        variants={dropIn}
        initial="hidden"
        animate="visible"
        exit="exit"
      >
        <ModalText text={"text"} />
        <ModalButton onClick={() => closeModal()} label="Close" />
      </motion.div>
    </Backdrop>
  );
};

const ModalText = () => (
  <div className="modal-text">
    <h3>Покана</h3>
    <h5>
      С благодарност за отделения труд, разбирането, старанието и приятелското отношение каним: Богдан Богданов
    </h5>
  </div>
);

const ModalButton = ({ onClick, label }) => (
  <motion.button
    className="modal-button"
    type="button"
    whileHover={{ scale: 1.1 }}
    whileTap={{ scale: 0.95 }}
    onClick={onClick}
  >
    {label}
  </motion.button>
);

export default Modal;
